pre {
  /* font-size: 15px; */
  /* border: 2px solid grey; */
  width: 450px;
  border-left: 15px solid #6b7476;
  border-radius: 5px;
  padding: 14px;

  width: calc(100% - 50px);

  overflow: auto;
  
  
  /* Fixed line height */
  line-height: 24px;
  
  /* Use linear-gradient for background image */
  background-image: linear-gradient(180deg, #eee 50%, #fff 50%);
  
  /* Size background so that the height is 2x line-height */
  background-size: 100% 48px;
  
  /* Offset the background along the y-axis by top padding */
  background-position: 0 14px;

  counter-reset: line;

  box-shadow: inset 0 0 1px #000;

}


pre span:before {
  counter-increment: line;
  content: counter(line);
  display: inline-block;
  border-right: 1px solid #ddd;
  padding: 0 .5em;
  margin-right: .5em;
  color: #888;
  width: 1.5em; /* Fixed width */

}


@media print {  
  body, html {
    height: auto;
    height: initial;
    overflow: visible;
    overflow: initial;
  }

  body * {
    visibility: hidden;
  }
  #NOTICEME, #NOTICEME * {
    visibility: visible;
  }
  /* #NOTICEME {
    position: absolute;
    overflow: auto;
    left: 0;
    top: 0;
  } */
}



